* {
  font-size: 1.1rem;
}

.App {
  text-align: left;
  // position: relative;

  .Checked {
    background-color: #26f17b;
  }

  .Checking {
    background-color: #e69318;
  }

  .InProgress {
    background-color: #e3f126;
  }

  .ToDo {
    background-color: #bbbaba;
  }

  .failed {
    background-color: #f12626;
  }

  // .table {
  //   left: 50%;
  //   transform: translate(-50%, 0);
  // }

  @media screen and (max-width:1920px) and (min-width:1200px) {
    .table {
      left: 50%;
    transform: translate(-50%, 0);
      width: 90%;
      max-width: 300px;
      margin: auto;
      // overflow: hidden
      overflow-x: auto;
    } 

    .mentor-table {
      position: absolute;
      top: 80px;
      text-align: center;
    }
  
    .mentorTitle,
    .mentorName {
      min-width: 300px;
      font-weight: bold;
      font-size: 1.3em;
    }

    .task-table {
      position: absolute;           
      top: 140px;
      
      .taskName {
        min-width: 280px;
      }
    }
  
    .description-table {
      position: absolute;
      top: 520px;
  
      .description-table__cell {
        min-width: 180px;
        padding-left: 10px; 
      }
    }

    .css-10nd86i {
      position: absolute;
      top: 30px;
      left: 10%;
      width: 80%;
    }

    .cell {
      min-width: 100px;
      text-align: center;
    }
  }
  @media screen and (max-width:1199px) and (min-width:600px) {
    * {
      font-size: 0.9rem;
    }

    .table {
      left: 50%;
      transform: translate(-50%, 0);
      width: 90%;
      // max-width: 600px;
      margin: auto;
      // overflow-x: auto;
    } 

    .mentor-table {
      position: absolute;
      top: 80px;
      text-align: center;
    }
  
    .mentorTitle,
    .mentorName {
      min-width: 300px;
      font-weight: bold;
      font-size: 1.3em;
    }

    .wrapper {
      width: 500px;
      overflow-x: scroll;
      table-layout: fixed;
      position: absolute;           
        top: 140px;
    }

    .task-table {
      // position: absolute;           
      // top: 140px;
      // overflow-x: auto;

      .taskname {
        // min-width: 280px;
      }
    }
  
    .description-table {
      position: absolute;
      top: 520px;
  
      .description-table__cell {
        min-width: 180px;
        padding-left: 10px; 
      }
    }

    .css-10nd86i {
      position: absolute;
      top: 30px;
      left: 10%;
      width: 80%;
    }

    .cell {
      max-width: 90px;
      text-align: center;
      word-wrap: break-word;
    }
  }


  @media screen and (max-width:599px) and (min-width:300px) {
    .table {
      // left: 50%;
      // transform: translate(-50%, 0);
      // width: 90%;
      // // max-width: 500px;
      // margin: auto;
      // overflow-x: auto;
      // table-layout: fixed;
      // overflow: hidden
    }

    .mentor-table {
      left: 50%;
      transform: translate(-50%, 0);
      position: absolute;
      top: 80px;
      max-width: 300px;
      text-align: center;
    }

    .wrapper {
      left: 50%;
      transform: translate(-50%, 0);
      width: 350px;
      overflow-x: scroll;
      table-layout: fixed;
      position: absolute;           
      top: 140px;
    }

    .task-table {
      // position: absolute;           
      // top: 140px;
      // overflow: scroll;
      // max-width: 500px;
      // table-layout: fixed;
      // .taskname {
      //   min-width: 10px;
      // }
    }

    .mentorTitle,
    .mentorName {
    min-width: 40%;
    font-weight: bold;
    font-size: 1.3em;
  }

  .css-10nd86i {
    position: absolute;
    top: 30px;
    left: 10%;
    width: 80%;
  }

  .description-table {
    left: 50%;
    transform: translate(-50%, 0);
    position: absolute;
    min-width: 80%;
    top: 450px;

    .description-table__cell {
      min-width: 100px;
      padding-left: 10px; 
    }
  }

  .taskname {
    // max-width: 60px;
    padding: 0;
    // text-align: center;
    font-size: 0.6rem;
    word-wrap: break-word;
  }

  .cell {
    // max-width: 60px;
    text-align: center;
    font-size: 0.5rem;
    word-wrap: break-word;
  }

  * {
    font-size: 0.7rem;
  }
  } 

  .studentName {
    background-color: #59b3fc;
  }

 

  td {
    min-height: 25px;
  }

  .link {
    padding-left: 4px;
    padding-right: 4px;
    text-decoration: none;
    color: black;
  }

  .taskName {
    text-align: left;
  }
}
