body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-size: 1.1rem; }

.App {
  text-align: left; }
  .App .Checked {
    background-color: #26f17b; }
  .App .Checking {
    background-color: #e69318; }
  .App .InProgress {
    background-color: #e3f126; }
  .App .ToDo {
    background-color: #bbbaba; }
  .App .failed {
    background-color: #f12626; }
  @media screen and (max-width: 1920px) and (min-width: 1200px) {
    .App .table {
      left: 50%;
      -webkit-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
      width: 90%;
      max-width: 300px;
      margin: auto;
      overflow-x: auto; }
    .App .mentor-table {
      position: absolute;
      top: 80px;
      text-align: center; }
    .App .mentorTitle,
    .App .mentorName {
      min-width: 300px;
      font-weight: bold;
      font-size: 1.3em; }
    .App .task-table {
      position: absolute;
      top: 140px; }
      .App .task-table .taskName {
        min-width: 280px; }
    .App .description-table {
      position: absolute;
      top: 520px; }
      .App .description-table .description-table__cell {
        min-width: 180px;
        padding-left: 10px; }
    .App .css-10nd86i {
      position: absolute;
      top: 30px;
      left: 10%;
      width: 80%; }
    .App .cell {
      min-width: 100px;
      text-align: center; } }
  @media screen and (max-width: 1199px) and (min-width: 600px) {
    .App * {
      font-size: 0.9rem; }
    .App .table {
      left: 50%;
      -webkit-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
      width: 90%;
      margin: auto; }
    .App .mentor-table {
      position: absolute;
      top: 80px;
      text-align: center; }
    .App .mentorTitle,
    .App .mentorName {
      min-width: 300px;
      font-weight: bold;
      font-size: 1.3em; }
    .App .wrapper {
      width: 500px;
      overflow-x: scroll;
      table-layout: fixed;
      position: absolute;
      top: 140px; }
    .App .description-table {
      position: absolute;
      top: 520px; }
      .App .description-table .description-table__cell {
        min-width: 180px;
        padding-left: 10px; }
    .App .css-10nd86i {
      position: absolute;
      top: 30px;
      left: 10%;
      width: 80%; }
    .App .cell {
      max-width: 90px;
      text-align: center;
      word-wrap: break-word; } }
  @media screen and (max-width: 599px) and (min-width: 300px) {
    .App .mentor-table {
      left: 50%;
      -webkit-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
      position: absolute;
      top: 80px;
      max-width: 300px;
      text-align: center; }
    .App .wrapper {
      left: 50%;
      -webkit-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
      width: 350px;
      overflow-x: scroll;
      table-layout: fixed;
      position: absolute;
      top: 140px; }
    .App .mentorTitle,
    .App .mentorName {
      min-width: 40%;
      font-weight: bold;
      font-size: 1.3em; }
    .App .css-10nd86i {
      position: absolute;
      top: 30px;
      left: 10%;
      width: 80%; }
    .App .description-table {
      left: 50%;
      -webkit-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
      position: absolute;
      min-width: 80%;
      top: 450px; }
      .App .description-table .description-table__cell {
        min-width: 100px;
        padding-left: 10px; }
    .App .taskname {
      padding: 0;
      font-size: 0.6rem;
      word-wrap: break-word; }
    .App .cell {
      text-align: center;
      font-size: 0.5rem;
      word-wrap: break-word; }
    .App * {
      font-size: 0.7rem; } }
  .App .studentName {
    background-color: #59b3fc; }
  .App td {
    min-height: 25px; }
  .App .link {
    padding-left: 4px;
    padding-right: 4px;
    text-decoration: none;
    color: black; }
  .App .taskName {
    text-align: left; }

